import app from '../../App.vue'
export default {
	data() {
		return {
			imageSrc: [0, 1, 2, 3],
			learnPage: ["图书馆","成绩管理","自习助手","四六级"],
			livePage: ["校园地图","故障报修","失物招领","通讯录"],
			pageTypeName: ["诚信雨伞", "校园巴士", "校园网"],
			playPage: ["南华头像", "校园直播"]
		}
	},
	methods: {
		scanCode() {
			uni.scanCode({
				onlyFromCamera: true,
				scanType: ['qrCode'],
				success(res) {
					console.log(res)
				},
				fail(res) {
					console.log(res)
					uni.showToast({
						title: '扫码失败，请重试',
						icon: 'none',
						duration: 2000
					})
				}
			})
		},
		learnToPage(index){
			let url = '';
			switch (index) {
				case 0:
					url = 'library/library'
					break;
				case 1:
					url = 'scoreSystem/scoreSystem'
					break;
				case 2:
					url = 'reviewLesson/reviewLesson'
					break;
				case 3:
					url = 'cetQuery/cetQuery'
					break;
			}
			uni.navigateTo({
				url: url
			})
		},
		livePageName(index){
			let url = '';
			switch (index) {
				case 0:
					url = 'schoolMap/schoolMap'
					break;
				case 1:
					url = './repair/repair'
					break;
				case 2:
					url = 'lostThing/lostThing'
					break;
				case 3:
					url = 'phoneList/phoneList'
					break;
			}
			uni.navigateTo({
				url: url
			})
		},
		
		ToPageName(index) {
			let url = '';
			switch (index) {
				case 0:
					url = "honestyUmbrella/honestyUmbrella"
					break;
				case 1:
					url = "honestyUmbrella/honestyUmbrella"
					break;
				case 2:
					url = "schoolWiFi/schoolWiFi"
					break;
			}
			uni.navigateTo({
				url: url
			})
		},
		playPageName(index){
			let url = '';
			switch (index) {
				case 0:
					url = 'schoolPicture/schoolPicture'
					break;
				case 1:
					url = 'schoolProgram/schoolProgram'
					break;
			}
			uni.navigateTo({
				url: url
			})
		}
	},
	onLoad() {
		uni.showModal({
			content:'功能还未完善'
		})
	}
}
